<h1 id="easywechat" tabindex="-1"><a class="header-anchor" href="#easywechat" aria-hidden="true">#</a> EasyWeChat</h1>
<p>EasyWeChat 是一个开源的 <a href="http://www.wechat.com" target="_blank" rel="noopener noreferrer">微信<OutboundLink/></a> 非官方 SDK。</p>
<p>EasyWeChat 的安装非常简单，因为它是一个标准的 <a href="https://getcomposer.org/" target="_blank" rel="noopener noreferrer">Composer<OutboundLink/></a> 包，这意味着任何满足下列安装条件的 PHP 项目支持 Composer 都可以使用它。</p>
<h3 id="环境需求" tabindex="-1"><a class="header-anchor" href="#环境需求" aria-hidden="true">#</a> 环境需求</h3>
<blockquote>
<ul>
<li>PHP &gt;= 7.4</li>
<li><a href="http://php.net/manual/en/book.curl.php" target="_blank" rel="noopener noreferrer">PHP cURL 扩展<OutboundLink/></a></li>
<li><a href="http://php.net/manual/en/book.openssl.php" target="_blank" rel="noopener noreferrer">PHP OpenSSL 扩展<OutboundLink/></a></li>
<li><a href="http://php.net/manual/en/book.simplexml.php" target="_blank" rel="noopener noreferrer">PHP SimpleXML 扩展<OutboundLink/></a></li>
<li><a href="http://php.net/manual/en/book.fileinfo.php" target="_blank" rel="noopener noreferrer">PHP fileinfo 拓展<OutboundLink/></a></li>
</ul>
</blockquote>
<h3 id="加入我们" tabindex="-1"><a class="header-anchor" href="#加入我们" aria-hidden="true">#</a> 加入我们</h3>
<p><a href="http://shang.qq.com/wpa/qunwpa?idkey=b4dcf3ec51a7e8c3c3a746cf450ce59895e5c4ec4fbcb0f80c2cd97c3c6e63e9" target="_blank" rel="noopener noreferrer">EasyWeChat SDK 交流群<OutboundLink/></a> ID: 319502940</p>
<blockquote>
<p>{warning} 为了避免广告及不看文档用户，加群需要付费，所以请使用 能支持群费的客户端。
另外：付费加群不代表我们有责任在群里回答你的问题，所以请认真阅读微信官方文档与 SDK 使用文档再使用，否则提的低级问题不会有人理你
不喜勿加，谢谢！
除非你发现了明确的 Bug，否则不要在群里 @ 我 🙏</p>
</blockquote>
<p>你有以下两种方式加入到我们中来，为广大开发者提供更优质的免费开源的服务：</p>
<blockquote>
<ul>
<li><strong>贡献代码</strong>：我们的代码都在 <a href="https://github.com/overtrue/wechat" target="_blank" rel="noopener noreferrer">overtrue/wechat<OutboundLink/></a> ，你可以提交 PR 到任何一个项目，当然，前提是代码质量必须是 OK 的。</li>
<li><strong>翻译或补充文档</strong>：我们的文档在：<a href="https://github.com/easywechat/docs/" target="_blank" rel="noopener noreferrer">EasyWeChat/docs<OutboundLink/></a>，你可以选择补充文档或者参与英文文档的翻译，目前有 <code>zh-cn</code> 与 <code>en</code> 两个分支，你可以提交对应的 PR 到目标分支参与翻译工作。</li>
</ul>
</blockquote>
<h3 id="开始之前" tabindex="-1"><a class="header-anchor" href="#开始之前" aria-hidden="true">#</a> 开始之前</h3>
<p>我们提供了视频教程：https://www.easywechat.com/tutorials 当然，我还是建议你具备以下基础知识，否则可能没有那么快上手。</p>
<p>本 SDK 不是一个全新再造的东西，所以我不会从 0 开始教会你开发微信，你完全有必要在使用本 SDK 前做好以下工作：</p>
<blockquote>
<ul>
<li>具备 PHP 基础知识，不要连闭包是啥都不明白，可以参考我在知乎的回答: <a href="http://www.zhihu.com/question/26635323/answer/33812516" target="_blank" rel="noopener noreferrer">想要开发自己的PHP框架需要那些知识储备？<OutboundLink/></a></li>
<li>熟悉 PHP 常见的知识：自动加载、composer 的使用、JSON 处理、Curl 的使用等；</li>
<li><strong>仔细阅读并看懂</strong><a href="http://mp.weixin.qq.com/wiki/13/80a1a25adbc46faf2716774c423b3151.html" target="_blank" rel="noopener noreferrer">微信官方文档<OutboundLink/></a> <a href="https://open.weixin.qq.com/cgi-bin/showdocument?action=dir_list&amp;t=resource/res_list&amp;verify=1&amp;id=open1419318292&amp;token=&amp;lang=zh_CN" target="_blank" rel="noopener noreferrer">微信开放平台文档<OutboundLink/></a>；</li>
<li>明白微信接口的组成，自有服务器、微信服务器、公众号（还有其它各种号）、测试号、以及通信原理（交互过程）；</li>
<li>了解基本的 HTTP 协议，Header 头、请求方式（GET\POST\PUT\PATCH\DELETE）等；</li>
<li>基本的 Debug 技能，查看 php 日志，nginx 日志等。</li>
</ul>
</blockquote>
<p>如果你不具备这些知识，请不要使用，因为用起来会比较痛苦。</p>
<p>另外你有必要看一下以下的链接：</p>
<blockquote>
<ul>
<li>https://learnku.com/laravel/t/535/assertion-people-who-do-not-understand-the-wisdom-of-asking-questions-will-not-graduate-from-junior-programmers</li>
<li>http://laravel-china.github.io/php-the-right-way/</li>
</ul>
</blockquote>
<p>如果你在群里问以下类似的问题，这真的是你没有做好上面的工作：</p>
<blockquote>
<ul>
<li>&quot;为啥我的不行啊，请问服务器日志怎么看啊？&quot;</li>
<li>&quot;请问这是什么原因啊？[结果/报错截图]&quot;</li>
<li>&quot;请问这个SDK怎么用啊？&quot;</li>
<li>&quot;谁能告诉我这个SDK是怎么安装的啊？&quot;</li>
<li>&quot;怎么接收用户发的消息啊？&quot;</li>
<li>&quot;为啥我的报这个错啊：Class XXXX not found...&quot;</li>
<li>...</li>
</ul>
</blockquote>
<p>我们专门针对一些容易出现的通用问题已经做了汇总： <a href="/docs/master/troubleshooting">疑难解答</a> ，如果你在问题疑难解答没找到你出现的问题，那么可以在这里提问 <a href="https://github.com/overtrue/wechat/issues" target="_blank" rel="noopener noreferrer">GitHub<OutboundLink/></a>，提问请描述清楚你用的版本，你的做法是什么，不然别人没法帮你。</p>
<blockquote>
<p>{warning} 最后，请 <strong>不要在QQ单独找我提问</strong>，除非你是发现了明显的bug。有问题先审查代码，看文档, 再 google，然后 去群里发个问题，带上你的代码，重现流程，大家有空的会帮忙你解答。谢谢合作！🙏</p>
</blockquote>
<h3 id="打赏支持" tabindex="-1"><a class="header-anchor" href="#打赏支持" aria-hidden="true">#</a> 打赏支持</h3>
<p>这是一个开源的项目，我们没有收费服务，你如果觉得你从中获益，简化了你的开发工作，你可以 <a href="/donate">打赏</a> 来支持我们。</p>
